.login-wrap {
  display: flex;
  flex-direction: column;
}
.log {
  font-size: 24px;
  font-weight: 700;
}
.log-text {
  font-size: 17px;
  font-weight: 500;
  padding-top: 22px;
  width: 334px;
  color: #304fff;
  text-align: center;
  padding-bottom: 11px;
}
.label1 {
  display: flex;
  flex-direction: column;
  padding-bottom: 31px;
}
.label1 h5 {
  padding-bottom: 7px;
  font-size: 17px;
  font-weight: 500;
  color: #787993;
}
.label1 input {
  width: 329px;
  height: 62px;
  border-radius: 5px;
  border: 1px solid #9797977b;
}
.label1 input::placeholder {
  font-size: 19px;
  font-weight: 500;
  color: #c7c7c7;
  
}
.label2 {
  padding-bottom: 31px;
}
.label2 h5 {
  padding-bottom: 7px;
  padding-top: 24px;
  font-size: 17px;
  font-weight: 500;
  color: #787993;
}
.label2 input {
  width: 329px;
  height: 62px;
  border-radius: 5px;
  border: 1px solid #9797977b;
}
.label2 input::placeholder {
  font-size: 19px;
  font-weight: 500;
  color: #c7c7c7;
  padding-left: 24.5px;
}
.enter {
  color: #ffffff;
  font-size: 17px;
  font-weight: 500;
  background: #41d481;
  width: 329px;
  height: 62px;
  border: none;
  border-radius: 5px;
}
.dis-input {
  display: flex;
  align-items: center;
  width: 331px;
  justify-content: space-between;
  padding-top: 49px;
}
.dis-input input {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #979797;
}
.dis-input h6 {
  color: #787993;
  font-size: 14px;
  font-weight: 500;
  width: 50px;
}
.h6-TEXT {
  padding-top: 16px;
  width: 333px;
  text-align: center;
  color: #4e72ca;
  font-size: 16px;
  font-weight: 700;
}
.login-dis {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 101px;
}
.color {
  border-radius: 220px 220px 220px 25px;
  background: #ffffff;
  width: 275px;
  height: 275px;
  margin-top: 372px;
  margin-left: 61px;
}
.bg-img {
  background-image: url(../../assets/bg-login.png);
  width: 654px;
  height: 952px;
}
.kivi {
  padding-top: 79px;
  padding-left: 42px;
  color: #41d481;
  font-size: 30px;
}
.tag-br {
  color: #787993;
  font-size: 19px;
  font-weight: 700;
  padding-left: 41.46px;
  padding-top: 7.62px;
  padding-bottom: 18.92px;
}
.kak {
  color: #41d481;
  font-size: 16px;
  font-weight: 700;
  width: 162px;
  height: 40px;
  border: 2px solid #41d481;
  background-color: transparent;
  border-radius: 5px;
  margin-left: 41.46px;
}
.kak:hover {
  background-color: #41d481;
  color: #ffffff;
  transition: all ease-in-out 0.5s;
}
.img-dis2 {
  display: flex;
  align-items: center;
  width: 180px;
}
