* {
  list-style: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Gilroy";
}
// @font-face {
//     font-family: 'Gilroy';
//     src: url(../../../fonts/Gilroy-Regular.ttf);
// }
.container {
  max-width: 1144px;
  margin: 0 auto;
  padding: 0 15px;
}
footer {
  padding-top: 72px;
  background: #33383c;
}
.ul_display {
  padding-top: 52px;
  display: flex;
  justify-content: space-between;
}
.first-li {
  color: #ffffff;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0px;
}
.ul_display1 {
  display: flex;
  flex-direction: column;
  row-gap: 12px;
}
.two_li {
  font-size: 14px;
  font-weight: 500;
  text-align: left;
  color: #878f99;
  cursor: pointer;
}
.two_li:hover{
  color: #00f3df;

}
.ul_display3 {
  display: flex;
  justify-content: space-between;
  padding-top: 52px;
  padding-bottom: 40px;
}
.footer-text-small {
  padding-bottom: 47px;
  font-size: 15px;
  font-weight: 500;
  line-height: 18px;
  margin: 0 auto;
  color: #98a4b7;
  width: 302px;
}
.img-h {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 34px;
  column-gap: 9px;
}
.img-h h5 {
  color: #66717a;
  font-size: 15px;
}
hr {
  border: 1px solid #4c565e;
  background: #4c565e;
}
.end-footer {
  padding-top: 27px;
  padding-bottom: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.end-footer__li {
  color: #63717b;
  font-size: 15px;
}
.end-footer__li:hover {
  color: #00f3df;
  font-size: 15px;
}
