.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100px;
  height: 98px;

  &__text {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 150px;

    &--title {
      color: #4a4a4a;
      font-family: Gilroy;
      font-size: 14px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      padding-top: 6px;
    }
  }
}

.purple {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  background: #b6cafb;
  width: 75px;
  height: 75px;
}

