.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;

  &__chapters {
    display: flex;
    align-items: center;
    width: 100%;
    height: calc(129px + 45px);
  }
  &__ct {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__icon--link {
    text-decoration: none;
  }
}

.sectionAds {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  &__ct {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__posts {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 25px;
    gap: 25px 0 ;
  }

  &__text {
    display: flex;
    border-bottom: 3px solid #d5d5d5;
    &--title {
      color: #4e72ca;
      font-family: Gilroy;
      font-size: 24px;
      font-style: normal;
      font-weight: 900;
      line-height: normal;

      border-bottom: 2px solid #4e72ca;
      //   width: 128px;
      //   height: 3px;
      //   flex-shrink: 0;
    }

    &--secondTitle {
      color: #9b9b9b;
      font-family: Gilroy;
      font-size: 24px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      padding-left: 77px;
    }
  }
}

.section {
  &__card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 262px;
    height: 370px;
    flex-shrink: 0;

    &--img {
      width: 262px;
      height: 230px;
      border-radius: 15px;
      background: #d8d8d8;
      overflow: hidden;
    }


    &--title {
      color: #000;
      font-family: Gilroy;
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      padding-top: 4px;
    }
    &--pretitle {
      color: #4a4a4a;
      font-family: Gilroy;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      padding-top: 8px;
      padding-bottom: 6px;
    }

    &--price {
      color: #000;
      font-family: Gilroy;
      font-size: 20px;
      font-style: bold;
      font-weight: 700;
      line-height: normal;
    }
  }
}

.main {
  display: flex;
  align-items: center;
  justify-content: end;
  width: 100%;
  height: 330px;
  flex-shrink: 0;
  background: rgba(182, 202, 251, 0.16);
  box-shadow: 0px 21px 13px 0px rgba(182, 202, 251, 0.23) inset;
  overflow: hidden;

  &__cards {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    width: calc(359px + 913px);
    height: 238px;
  }

  &__card {
    // width: 359px;
    height: 238px;
    flex-shrink: 0;
    border-radius: 10px;
    background: #b4e2e1;
  }

  &__ct {
    display: flex;
    align-items: center;
    justify-content: end;
  }
}


.sectionAds {
  &__posts {
    overflow: hidden;
  }
  &__cards {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 50px 0;
  }

  .sectionAds__card {
    width: 250px;
    height: 300px;
    background-color: #4e72ca;
  }
}

.accordion {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 50px 0;
}
.cards {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.details {
  display: flex;
  align-items: center;

  display: flex;
  align-items: center;
  color: #4e72ca;
  font-family: Gilroy;
  font-size: 15px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  border: none;

  .image {
    padding-left: 5px;
  }
  background: transparent;
  padding-top: 20px;
}
