.special {
  display: flex;

  &__buttons {
    display: flex;
    justify-content: space-between;
    max-width: 430px;
    width: 100%;
    padding-top: 24px;
  }
  &__author {
    display: flex;
    padding-top: 17px;

    &--btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 170px;
      height: 47px;
      flex-shrink: 0;
      border-radius: 23.5px;
      background: #304fff;
      text-decoration: none;
      //
      color: #fff;
      font-family: Gilroy;
      font-size: 14px;
      font-style: normal;
      font-weight: 800;
      line-height: normal;
    }
    &--btn2 {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 222px;
      height: 47px;
      flex-shrink: 0;
      border-radius: 23.5px;
      background: #4e72ca;
      text-decoration: none;
      //
      color: #fff;
      font-family: Gilroy;
      font-size: 14px;
      font-style: normal;
      font-weight: 800;
      line-height: normal;
    }
    &--text {
      color: #000;
      font-family: Gilroy;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
    &--link {
      color: #2aae63;
      font-family: Gilroy;
      font-size: 14px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      text-decoration-line: underline;
      padding-left: 7px;
    }
  }

  &__num {
    position: relative;
    display: flex;
    align-items: center;
    height: 60px;
    padding-top: 14px;
    &--text {
      cursor: pointer;
      display: block;
      border-radius: 18.5px;
      background: #65e99f;
      padding: 9px 200px 9px 23px;
      flex-shrink: 0;

      color: #000;
      font-family: Gilroy;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
    }
    &--btn {
      position: absolute;
      left: 200px;
      width: 190px;
      height: 37px;
      flex-shrink: 0;
      border-radius: 18.5px;
      background: #41d481;
      border: none;
      cursor: pointer;
    }
  }

  &__images {
    display: flex;
    width: 50%;

    &--little {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 461px;
    }
  }
  &__little {
    width: 113px;
    height: 75px;
    flex-shrink: 0;
  }

  &__littleImg {
    width: 75px;
    height: 75px;
    flex-shrink: 0;
  }
  &__img {
    width: 456px;
    height: 456px;
    flex-shrink: 0;
  }

  &__text {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding-left: 32px;

    &__location {
      color: #4e72ca;
      font-family: Gilroy;
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      text-decoration-line: underline;
    }

    &--pretitle {
      color: #414d6a;
      font-family: Gilroy;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }

    &--articul {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &--pricebox {
      width: 100%;
      padding-top: 17px;
      padding-bottom: 8px;
    }
    &--price {
      color: #444;
      font-family: Gilroy;
      font-size: 40px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }
  }

  &__pretitle {
    width: 100%;
    &--text {
      max-width: 556px;
      width: 100%;
      color: #414d6a;
      font-family: Gilroy;
      font-size: 17px;
      font-style: normal;
      font-weight: 500;
      line-height: 25px; /* 147.059% */
      padding-top: 36px;
    }
  }

  &__review {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60%;
    padding-top: 19px;
    padding-bottom: 97px;
    &--text {
      color: #000;
      font-family: Gilroy;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }

    &--link {
      color: #d0021b;
      font-family: Gilroy;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      text-decoration: none;
    }
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  &__detailed {
    display: flex;
    flex-direction: column;

    &--text {
      padding-top: 78px;
      padding-bottom: 14px;
    }
    &--title {
      color: #414d6a;
      font-family: Gilroy;
      font-size: 34px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }
  }
}

.table {
  width: 100%;
  border: none;
  box-shadow: #000;
  padding-top: 25px;

  &__line {
    display: flex;
    border: 1px solid rgba(151, 151, 151, 0.25);
  }
  &__heading {
    display: flex;
    align-items: center;
    justify-content: start;
    width: 200px;
    padding-left: 13px;
    padding-top: 17px;
    padding-bottom: 5px;
    border-right: 1px solid rgba(151, 151, 151, 0.25);
  }
  &__title {
    color: #444;
    font-family: Gilroy;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  &__text {
    display: flex;
    align-items: center;
    justify-content: start;
    color: #444;
    font-family: Gilroy;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    // padding-left: 14px;

    &--link {
      color: #4e72ca;
      font-family: Gilroy;
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      text-decoration-line: underline;
    }
  }

  &__header {
    color: #444;
    font-family: Gilroy;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding-left: 14px;
    padding-top: 17px;
    padding-bottom: 5px;
  }

  &__pretitle {
    display: flex;
    align-items: center;
    justify-content: start;
    color: #444;
    font-family: Gilroy;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    padding-left: 14px;
  }
  &__list {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
  }

  .placelink {
    max-width: 536px;
    width: 100%;
  }
  .tr {
    display: flex;
    flex-direction: column;
    align-items: start;
  }
}

.section__card--titles {
  color: #000;
  font-family: Gilroy;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  padding-top: 10px;
}

.section__card--pretitles {
  color: #4a4a4a;
  font-family: Gilroy;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  padding-top: 8px;
}

.section__card--prices {
  color: #000;
  font-family: Gilroy;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  padding-top: 4px;
}
.section__card{
  border: 1px solid rgba(151, 151, 151, 0.25);
border-radius: 20px;
}

.section__card--text{
  padding-left: 14px;
}