.nav {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 67px;
  &__ct {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: calc(425px + 79px + 76px);
  }
  &__bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 425px;
    height: 100%;
  }
  &__link {
    color: #000;
    font-family: Gilroy;
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-decoration: none;
  }

  &__login {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 180px;
    width: 100%;
  }
  &__between {
    color: #4e72ca;
    font-family: Gilroy;
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  &__reg {
    color: #4e72ca;
    font-family: Gilroy;
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  &__log {
    color: #4e72ca;
    font-family: Gilroy;
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  &__logos {
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    &--text {
      color: #000;
      font-family: Gilroy;
      font-size: 15px;
      font-style: normal;
      font-weight: 700;
      line-height: 17px; /* 113.333% */
      max-width: 162px;
      width: 100%;
      padding-left: 14px;
    }
  }

  &__radio {
    max-width: calc(79px + 66px);
    width: 100%;

    &--lang {
      color: #4a4a4a;
      font-family: Gilroy;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }

    &__none {
      visibility: hidden;
      display: none;
    }
  }
}

.navLang-dis {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 30px;
}
.navLang-dis h5 {
  order: 1;
  font-weight: 500;
  font-size: 14px;
}
.img-icon {
  position: absolute;
  left: 0;
}

.navLang-dis__pretitle {
  padding-right: 50px;
}
.logout {
  display: none;
  width: 200px;
  position: absolute;
  top: 0;
  left: 50px;
  background: rgba(182, 202, 251, 0.16);
  box-shadow: 0px 21px 13px 0px rgba(182, 202, 251, 0.23) inset;
  border-radius: 20px;
  transition: all ease-in-out 1s;
  padding-left: 10px;
  padding-top: 9px;
  padding-bottom: 9px;
}

.parent {
  position: relative;
}
.img-dis {
  cursor: pointer;
}

.img-dis:hover + .logout {
  display: block;
  align-items: center;
  justify-content: center;
  background-color: #fff;
}
.red__button {
  color: red;
  font-family: Gilroy;
  font-size: 20px;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
  padding-left: 10px;
  padding-right: 10px;
  border: none;
  background: transparent;
}

.toProfile {
  color: #4e72ca;
  font-family: Gilroy;
  font-size: 20px;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
  text-decoration: none;
}
.nav-logo {
  display: flex;
  align-items: center;
}
.nav-logo h5 {
  color: #000;
  padding-left: 14px;
  font-size: 15px;
  font-weight: 700;
  width: 159px;
}
