.error {
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: center;
  width: 100%;
  background-image: url(../../assets/headerBg/1650019949_8-kartinkof-club-p-ups-kartinki-prikolnie-8.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 100%;

  &__ct {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    width: 90%;
  }
  &__title {
    max-width: 500px;
    width: 100pp;
    color: #4f4f4f;
    font-family: Gilroy;
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding-top: 230px;
  }

  &__pretitle {
    color: #4f4f4f;
    font-family: Gilroy;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding-top: 10px;
  }
  .link {
    color: red;
    font-family: Gilroy;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding-bottom: 100px;
    padding-top: 10px;
  }
}
